<template>
  <div
    id="app"
    @dragover.prevent="checkDrag($event, 'new', true)"
    @dragleave.prevent="checkDrag($event, 'new', false)"
    @drop.prevent="onDrop"
  >
    <p>lottieのjsonをdropしてください。</p>
    <div id="container"></div>
  </div>
</template>

<script>
import lottie from "lottie-web";

export default {
  name: "app",
  components: {},
  data() {
    return {
      renderer: "svg",
      loop: true,
      autoplay: true,
      path: "lottie/data.json",
      animationData: undefined
    };
  },
  mounted() {
    this.load();
  },

  methods: {
    load() {
      let options = {
        container: document.getElementById("container"),
        renderer: this.renderer,
        loop: this.loop,
        autoplay: this.autoplay,
        animationData: this.animationData
      };
      if (!this.animationData) {
        options.path = this.path;
      }
      this.o = lottie.loadAnimation(options);
    },

    checkDrag(event, key, status) {
      //★④
      if (status && event.dataTransfer.types == "text/plain") {
        //ファイルではなく、html要素をドラッグしてきた時は処理を中止
        return false;
      }
      this.isDrag = status ? key : null;
    },

    onDrop(e) {
      this.isDrag = null; //★ドラッグ中のクラスを外しておく。
      let fileList = e.target.files ? e.target.files : e.dataTransfer.files; //★①ファイル取得
      // ファイルが無い時は処理を中止
      if (fileList.length == 0) {
        return false;
      }
      let f = fileList[0];
      if (f.type.indexOf("json") === -1) {
        alert("Bodymovieを使って出力された.jsファイルを読み込んでください");
      } else {
        var reader = new FileReader();

        // Closure to capture the file information.
        reader.onload = e => {
          console.log(e);
          this.animationData = JSON.parse(e.target.result);
          let container = document.getElementById("container");
          container.innerHTML = "";
          this.load();
        };

        // Read in the image file as a data URL.
        reader.readAsText(f);
      }
      console.log(fileList);
    }
  }
};
</script>

<style lang="scss">
#app {
  position: relative;
  // font-family: "Avenir", Helvetica, Arial, sans-serif;
  // -webkit-font-smoothing: antialiased;
  // -moz-osx-font-smoothing: grayscale;
  padding: 0;
  margin: 0;
  text-align: center;
  // color: #2c3e50;
  // margin-top: 60px;
  // display: flex;
  // justify-content: center;
  // align-items: center;
  height: 100vh;
  background: #eee;

  p {
    position: absolute;
    top: 0;
    width: 100%;
    z-index: 10;
  }
}

#container {
  padding-top: 50px;
  width: 100%;
  height: calc(100vh - 50px);
}
</style>
